<template>
	<b-list-group-item button class="d-flex align-items-center justify-content-between user-select-none" @click="clicked">
		<!-- v-on="$listeners" -->
		<div class="available_date d-flex align-items-center">
			<BIcon icon="calendar-check" width="1rem" scale="1.5" variant="success" v-if="available.intervals.length" />
			<BIcon icon="calendar-x" width="1rem" scale="1.5" class="text-muted" v-else />
			<span class="ml-3">{{ $moment(date).format("ddd, MMM D") }}</span>
		</div>
		<div class="available_time d-flex align-items-center text-muted">
			<BIcon icon="arrow-repeat" width="1rem" scale="1.2" class="text-muted mx-2" v-if="isRepeated" />
			<div class="text-right">
				<div v-for="(interval, i) in available.intervals" :key="i">
					{{ intervalStr(interval) }}
				</div>
			</div>
			<BIcon icon="chevron-right" class="ml-3" width="1rem" scale="1" />
		</div>
	</b-list-group-item>
</template>

<script>
	import { rrulestr } from "rrule";

	export default {
		props: {
			date: { type: Date },
			availability: { type: Array, required: true },
			until: { type: Date },
		},

		mounted() {
			// console.log(rrulestr("RDATE:20220730T000000Z"));
		},

		computed: {
			mDate() {
				return this.$moment(this.date).utc().startOf("date");
			},

			intervalsOptions() {
				return [...this.$store.state.invites.intervalsStartOptions, ...this.$store.state.invites.intervalsEndOptions];
			},

			isRepeated() {
				return this.available.repeats != "only";
			},

			available() {
				let intervals = [],
					repeats = "only";

				for (let available of this.availability) {
					let { rrule, duration } = available;

					let rule = rrulestr(rrule);

					let { dtstart, until, count, byweekday, byhour, byminute = 0 } = rule.origOptions;

					dtstart = this.$moment(dtstart).utc().startOf("date");

					until = until || this.until;

					if (until) {
						until = this.$moment(until).utc().startOf("date");
					}

					if (count == 1) {
						if (dtstart.isSame(this.mDate, "day")) {
							let start = byhour + byminute / 60;

							let end = start + duration / 60;

							intervals.push({ start, end });

							repeats = "only";

							continue;
						}
					}

					if (!byweekday) continue;

					if (byweekday) {
						let weekdays = byweekday.map((day) => day.weekday);

						let weekday = this.mDate.clone().weekday();

						if (!weekdays.includes(weekday)) continue;

						let date = this.mDate.clone().weekday(weekday);

						if (!date.isSame(this.mDate)) continue;

						let start = byhour + byminute / 60;

						let end = start + duration / 60;

						intervals.push({ start, end });

						repeats = weekdays.length > 1 ? "weekdays" : "all";

						continue;
					}
				}

				return { intervals, date: this.mDate.toDate(), repeats };
			},
		},

		methods: {
			intervalStr({ start, end }) {
				let intervalStart = this.$moment().set(this.hourAndMinute(start)).format("h:mm a");

				let intervalEnd = this.$moment().set(this.hourAndMinute(end)).format("h:mm a");

				return `${intervalStart} - ${intervalEnd}`;
			},

			hourAndMinute(num) {
				let hour = Math.floor(num);
				let minute = Math.floor((num - hour) * 60);

				return { hour, minute };
			},

			clicked() {
				this.$emit("click", this.available);
			},
		},
	};
</script>

