<template>
	<b-dropdown variant="text" left toggle-class="fs-sm outline-0 box-shadow-none px-0" menu-class="timezones_menu">
		<template #button-content>
			<EarthIcon width="1.3rem" style="fill: #5e5e5e" class="mx-1" />
			<span class="name font-weight-500 mx-1" style="color: #5e5e5e">{{ selectedOption.name }}</span>
			<span class="time text-muted mx-1">({{ getTimeByTimezone(selectedOption.iana) }})</span>
		</template>
		<b-dropdown-item v-for="(item, index) in options" :key="index" link-class="px-3 py-2" @click="select(item)">
			<div class="d-flex align-items-center justify-content-between fs-sm">
				<span class="name font-weight-500 mx-1">{{ item.name }}</span>
				<span class="time text-muted">{{ getTimeByTimezone(item.iana) }}</span>
			</div>
		</b-dropdown-item>
	</b-dropdown>
</template>

<script>
	import timezones from "@/data/timezones";
	import EarthIcon from "@/components/icons/earth.vue";
	export default {
		components: { EarthIcon },

		props: {
			value: { type: [String, Number] },
		},

		data() {
			return {
				now: new Date(),
			};
		},

		mounted() {
			setInterval(() => {
				this.now = new Date();
			}, 1000);

			if (!this.value) {
				let timezone = Intl.DateTimeFormat().resolvedOptions().timeZone;

				if (timezone) {
					this.select(timezones.find((item) => item.iana === timezone));
				} else {
					this.select(timezones[0]);
				}
			}
		},

		computed: {
			options() {
				return timezones;
				// return timezones.sort((a, b) => {
				// 	if (a.name < b.name) {
				// 		return -1;
				// 	}
				// 	if (a.name > b.name) {
				// 		return 1;
				// 	}
				// 	return 0;
				// });
			},

			selectedOption() {
				return this.options.find((item) => item.iana === this.value) || this.options[0];
			},
		},

		methods: {
			select(item) {
				this.$emit("input", item.iana);
				this.$emit("change", item);
			},

			getTimeByTimezone(timezone) {
				return this.$moment().tz(timezone).format("h:mm a");
			},
		},
	};
</script>

<style>
	.timezones_menu {
		max-height: 75vh;
		overflow-y: auto;
	}
</style>
